*{
    font-family: Helvetica, sans-serif;
    margin: 0;
    padding: 0;
}

body{
    padding: 40px;
    color: gray;
    background-color: #111;
}

input{
    width: 560px;
    max-width: 80vw;
    border: 0;
    border-radius: 4px;
    padding: 8px 16px;
    font-size: 1.2rem;
    color: #111;
    outline: none;
    margin-bottom: 64px;
}

.card-grid{
    display: flex;
    /* border: 1px red solid; */
    flex-wrap: wrap;
    gap: 16px;
}

.card{
    max-width: 240px;
    align-content: center;
    border-radius: 8px;
    overflow: hidden;
    background-color: rgb(22, 22, 24);
}

.card p{
    padding: 8px;
    text-align: center;
}

.card img{
    min-width: 240px;
    max-height: 170px;
}

h3{
    color: rgb(88, 226, 130);
    font-weight: 700;
    font-size: 1.8rem;
    margin-bottom: 8px;
    margin-top: 48px;
}

h1{
    color: white;
    font-size: 2.4rem;
    font-weight: 900;
    margin-bottom: 8px;
}